@-webkit-keyframes opacityAnimation {
    0% {
        opacity: 0;

    }
    100% {
        
    opacity: 1;

    }
}
@keyframes opacityAnimation {
    0% {
        opacity: 0;

    }
    100% {
        
    opacity: 1;

    }
}

.svg-figure{
    width: 25%;
    opacity: 0;
}

.svg-figure:nth-child(1){
    -webkit-animation:opacityAnimation 1s forwards 0s;
            animation:opacityAnimation 1s forwards 0s;
}
.svg-figure:nth-child(2){
    -webkit-animation:opacityAnimation 1s forwards 0.5s;
            animation:opacityAnimation 1s forwards 0.5s;
}
.svg-figure:nth-child(3){
    -webkit-animation:opacityAnimation 1s forwards 1.0s;
            animation:opacityAnimation 1s forwards 1.0s;
}
.svg-figure:nth-child(4){
    -webkit-animation:opacityAnimation 1s forwards 1.5s;
            animation:opacityAnimation 1s forwards 1.5s;
}
.svg-icon{
    width: 0;
    height: 0;
    opacity: 0;
    
}
.svg-icon:nth-child(1){
    -webkit-animation:growAbove 1s forwards 0s;
            animation:growAbove 1s forwards 0s;

}
.svg-icon:nth-child(2){
    -webkit-animation: growAbove 1s forwards 1s;
            animation: growAbove 1s forwards 1s;
}
.svg-icon:nth-child(3){
    -webkit-animation:growAbove 1s forwards 2s;
            animation:growAbove 1s forwards 2s;

}
.svg-icon:nth-child(4){
    -webkit-animation: growAbove 1s forwards 3s;
            animation: growAbove 1s forwards 3s;
}

@-webkit-keyframes growAbove {
    0% {
        width: 0;
    height: 0;
    opacity: 0;

    }
    100% {
        width: 45px;
    height: 45px;
    opacity: 1;

    }
  }

@keyframes growAbove {
    0% {
        width: 0;
    height: 0;
    opacity: 0;

    }
    100% {
        width: 45px;
    height: 45px;
    opacity: 1;

    }
  }
.svg-icon-span{
